<template>
  <v-card>
    <v-card-title v-text="`Edit Permissions`"></v-card-title>
    <v-card-text>
      <v-treeview
        :items="categorizedPermissions"
        v-model="activePermissions"
        item-key="_id"
        multiple-active
        selectable
        disabled
        @input="resource.prestine( false )"
      >
        <template v-slot:label="{ item }">
          <template v-if="item.name">
            <span v-text="item.name"></span>
          </template>
          <template v-else>
            <div>
              <h4 v-text="item.code" class="ma-0"></h4>
              <p v-text="item.description" class="ma-0"></p>
            </div>
          </template>
        </template>
      </v-treeview>
    </v-card-text>
    <v-card-actions>
      <v-btn color="primary" type="button" @click="submit" :disabled="resource.isPrestine">Save</v-btn>
    </v-card-actions>
  </v-card>
</template>

<script>
import Api from '../routes/api'
import { mapActions, mapGetters } from 'vuex'
import Resource from '@/core/models/Resource.js'

export default {
  props: [ 'categorizedPermissions', 'permissions' ],

  computed: {
    ...mapGetters({
      auth: 'auth/auth',
    }),

    activePermissions: { 
      get () { return this.permissions.map( item => item._id ) },
      set ( val ) { this.newPermissions = val }
    }
  },

  data: () => ({
    resource: new Resource,
    newPermissions: []
  }),

  methods: {
    ...mapActions({
      setSnackbar: 'snackbar/set',
      setAuth: 'auth/set',
    }),

    async submit () {
      const data = await this.resource.request({
        method: 'post',
        url: Api.permission.main( this.$route.params.id ),
        data: { permissions: this.newPermissions }
      })
      if( data ) {
        this.setSnackbar({
          model: true,
          text: 'Role permissions updated',
          timeout: 3000
        })

        this.resource.prestine()
      }
    },
  },
}
</script>